"use client";
import React, { useState } from "react";
import { db } from "../../../firebase/firebase";
import { collection, addDoc } from "firebase/firestore";

export default function CreateSuperAdmin() {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        role: 'superadmin'
    });
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        if (!formData.name || !formData.email) {
            setMessage('Vui lòng nhập đầy đủ thông tin!');
            return;
        }

        setLoading(true);
        try {
            const newAdmin = {
                name: formData.name,
                email: formData.email,
                role: formData.role,
                createdAt: new Date(),
            };
            
            await addDoc(collection(db, "adminUsers"), newAdmin);
            setMessage('Tạo Super Admin thành công!');
            setFormData({ name: '', email: '', role: 'superadmin' });
        } catch (error) {
            setMessage('Lỗi khi tạo Super Admin: ' + error.message);
        }
        setLoading(false);
    };

    return (
        <div className="min-h-screen bg-gray-100 flex items-center justify-center">
            <div className="bg-white p-8 rounded-lg shadow-lg w-full max-w-md">
                <h1 className="text-2xl font-bold text-center mb-6 text-red-600">
                    ⚠️ Tạo Super Admin
                </h1>
                
                <div className="bg-yellow-100 border border-yellow-400 text-yellow-700 px-4 py-3 rounded mb-4">
                    <strong>Lưu ý:</strong> Trang này chỉ dùng để tạo Super Admin đầu tiên. 
                    Sau khi tạo xong, hãy xóa trang này để bảo mật.
                </div>

                <form onSubmit={handleSubmit} className="space-y-4">
                    <div>
                        <label className="block text-gray-700 mb-2">Tên Super Admin</label>
                        <input
                            type="text"
                            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:border-blue-500"
                            value={formData.name}
                            onChange={(e) => setFormData({...formData, name: e.target.value})}
                            placeholder="Nhập tên Super Admin"
                        />
                    </div>

                    <div>
                        <label className="block text-gray-700 mb-2">Email</label>
                        <input
                            type="email"
                            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:border-blue-500"
                            value={formData.email}
                            onChange={(e) => setFormData({...formData, email: e.target.value})}
                            placeholder="Nhập email"
                        />
                    </div>

                    <div>
                        <label className="block text-gray-700 mb-2">Vai trò</label>
                        <select
                            className="w-full border border-gray-300 rounded px-3 py-2 focus:outline-none focus:border-blue-500"
                            value={formData.role}
                            onChange={(e) => setFormData({...formData, role: e.target.value})}
                        >
                            <option value="superadmin">Super Admin</option>
                            <option value="admin">Admin</option>
                            <option value="moderator">Moderator</option>
                        </select>
                    </div>

                    <button
                        type="submit"
                        disabled={loading}
                        className="w-full bg-red-600 text-white py-2 px-4 rounded hover:bg-red-700 disabled:opacity-50"
                    >
                        {loading ? 'Đang tạo...' : 'Tạo Admin'}
                    </button>
                </form>

                {message && (
                    <div className={`mt-4 p-3 rounded ${
                        message.includes('thành công') 
                            ? 'bg-green-100 text-green-700 border border-green-400' 
                            : 'bg-red-100 text-red-700 border border-red-400'
                    }`}>
                        {message}
                    </div>
                )}

                <div className="mt-6 text-center">
                    <a href="/admin" className="text-blue-600 hover:underline">
                        ← Quay lại Admin Panel
                    </a>
                </div>
            </div>
        </div>
    );
}